	import MxDatePicker from "@/components/mx-datepicker/mx-datepicker.vue";
 	export default {
		components: {
		    MxDatePicker
		},
 		data() {
 			return {
				itemList:[],
				shopid: 0,
				showPicker: false,
				time: '15:00:00',
				type: 'rangetime',
				value: ''           
 			}
 		},
 		onLoad:function(option){
 			this.shopid = option.shopid
 		},
 		onShow:function(){
 			var that = this
 			uni.request({
 				url: this.$bashUrl+ '/index.php/home/Api/time_list/',
 				method: 'POST',
 				data:{
					id: this.shopid
 				},
 				success:(res=>{
					that.itemList = res.data
					for(var i =0;i<res.data.length;i++){
						that.itemList[i].name = res.data[i].name.slice(0,5)
					}
					console.log(that.itemList)
 				})
 			})
 		},
 		methods: {
			onShowDatePicker(type){//显示
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
			},
			onSelected(e) {//选择
				this.showPicker = false;
				if(e) {
					this[this.type] = e.value; 
					//上传营业时间
					var that = this
					uni.request({
						url: this.$bashUrl+ '/index.php/home/Api/new_time/',
						method: 'POST',
						data:{
							id: this.shopid,
							time: this.time
						},
						success:(res=>{
							var that = this
							uni.request({
								url: this.$bashUrl+ '/index.php/home/Api/time_list/',
								method: 'POST',
								data:{
									id: this.shopid
								},
								success:(res=>{
									that.itemList = res.data
									for(var i =0;i<res.data.length;i++){
										that.itemList[i].name = res.data[i].name.slice(0,5)
									}
								})
							})
							uni.showToast({
								title: '时间添加成功',
								icon:"none"
							});
						}),
						fail() {
							uni.showToast({
								title:'时间添加失败',
								icon:'none'
							})
						}
					})
				}
			},
			Deletetable:function(id){
				var that = this
				uni.request({
					url: this.$bashUrl+ '/index.php/home/Api/del_time/',
					method: 'POST',
					data:{
						id: id
					},
					success:(res=>{
						var that = this
						uni.request({
							url: this.$bashUrl+ '/index.php/home/Api/time_list/',
							method: 'POST',
							data:{
								id: this.shopid
							},
							success:(res=>{
								that.itemList = res.data
								for(var i =0;i<res.data.length;i++){
									that.itemList[i].name = res.data[i].name.slice(0,5)
								}
							})
						})
						uni.showToast({
							title: '删除成功',
							icon:'none'
						})
					})
				})
			}
 		}
 	}
 